import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

import soal from './../data/assesment.json';
import jawaban from './../data/assesment_history.json';

import axios from "axios";
import tokenOrganizer from './../util/tokenOrganizer';
import Radio from '@mui/material/Radio';
import FormControlLabel from '@mui/material/FormControlLabel';

import configApi from './../config/api';
import { key } from "localforage";
import AssesmentCheckBox from "./AssesmentCheckBox";

function capitalizeFLetter(string) {
    return string.charAt(0).toUpperCase() +
        string.slice(1);
}
const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

export default function Page() {
    const [open, setOpen] = React.useState(false);
    const [nilai, setNilai] = React.useState(false);
    const handleOpen = () => {
        const reqSubmit = [];
        console.log(checkBoxJawaban);
        let checkBoxJawabanArray = Object.keys(checkBoxJawaban).map((key) => {
            return checkBoxJawaban[key]
        });
        checkBoxJawabanArray.forEach((v, i) => {
            if (typeof v.jawaban == "string" && v.jawaban != '') {
                reqSubmit.push({
                    id: v.id,
                    jawaban: v.jawaban
                })
            } else if (typeof v.jawaban == "object") {
                let jawabanArray = Object.keys(v.jawaban).map((key) => {
                    return v.jawaban[key];
                });
                let newJawabanArray = [];
                jawabanArray.forEach((v, i) => {
                    if (v != '') {
                        newJawabanArray.push(v)
                    }
                })
                let fixJawaban = newJawabanArray.join(",");
                if (fixJawaban != '') {
                    reqSubmit.push({
                        id: v.id,
                        jawaban: fixJawaban,
                    });
                }

            }
            if (v.nested_jawaban) {
                let result = Object.keys(v.nested_jawaban).map((key) => {
                    return v.nested_jawaban[key]
                });
                result.forEach((vr, ir) => {
                    if (vr.jawaban != '') {
                        reqSubmit.push({
                            id: vr.id,
                            jawaban: vr.jawaban,
                        })
                    }
                })
            }
        })

        if (reqSubmit.length == 0) {
            alert('Jawaban Kosong');
        }

        axios
            .post(`${configApi.BASE_URL}/assesment/${studi_kasus}/submit`, {
                jawaban: reqSubmit,
            }, {
                headers: {
                    Authorization: "Bearer " + tokenOrganizer.getToken()
                }
            })
            .then((response) => {
                setNilai(response.data.data.nilai)
                setOpen(true)
            }).catch((err) => {
                setOpen(true)
            });
    };
    const handleClose = () => {
        navigate(`/studi-kasus/${studi_kasus}`)
    };
    const navigate = useNavigate();

    const [mahasiswa, setMahasiswa] = useState({});
    const [nestedSoalFlagOpsi, setNestedSoalFlagOpsi] = useState({});
    const [nestedSoalFlagOpsiMaster, setNestedSoalFlagOpsiMaster] = useState({});
    const [nestedSoalFlag, setNestedSoalFlag] = useState(null);

    const [masterSoal, setMasterSoal] = useState([]);
    const [stepSoal, setStepSoal] = useState(0);
    const [currentSoal, setCurrentSoal] = useState(masterSoal[stepSoal]);
    const [textNext, setTextNext] = useState("Selanjutnya");
    const [opsiValue, setOpsiValue] = useState(0);
    const [calculateNilai, setCalculateNilai] = useState(0);
    // const [checkBox, setCheckBox] = useState(null);
    const [checkBoxJawaban, setCheckBoxJawaban] = useState({});
    const [jumlahSoal, setJumlahSoal] = useState(masterSoal.length);
    const [jumlahJawaban, setJumlahJawaban] = useState(0);
    const [percentage, setPercentage] = useState(((stepSoal + 1 / masterSoal.length) * 100).toFixed(0));
    const [isDijawab, setIsDijawab] = useState(0);
    const [firstLetterUppercase, setFirstLetterUppercase] = useState('');


    const params = useParams();
    const studi_kasus = params.studiKasus;

    useEffect(() => {
        setIsDijawab()
    }, [checkBoxJawaban])

    useEffect(() => {
        if (stepSoal == masterSoal.length - 1) {
            setTextNext("Selesai")
        } else {
            setTextNext("Selanjutnya")
        }
    }, [nestedSoalFlagOpsiMaster])

    useEffect(() => {
        if (stepSoal == masterSoal.length - 1) {
            setTextNext("Selesai")
        } else {
            setTextNext("Selanjutnya")
        }
    }, [stepSoal])

    useEffect(() => {
        if (currentSoal &&
            currentSoal.assesment_mapping_assesment_mapping_parent_assesment_idToassesment &&
            currentSoal.assesment_mapping_assesment_mapping_parent_assesment_idToassesment.length > 0) {
            currentSoal.assesment_mapping_assesment_mapping_parent_assesment_idToassesment.forEach(element => {
                console.log(element)
                // setNestedSoalFlagOpsi();
            });
        }
    }, [nestedSoalFlag])


    useEffect(() => {
        let token = tokenOrganizer.getToken();

        axios
            .get(`${configApi.BASE_URL}/assesment/${studi_kasus}/soal`, {
                headers: {
                    Authorization: "Bearer " + token
                }
            })
            .then((response) => {
                let soal_response = response.data.data;
                let blanko_form = {};
                let blanko_nested = {};
                soal_response.forEach((element, ix) => {
                    let single_blanko = {
                        id: element.id,
                        jawaban: '',
                    }
                    if (element.assesment_mapping_assesment_mapping_parent_assesment_idToassesment.length > 0) {
                        single_blanko["nested_jawaban"] = {};
                        element.assesment_mapping_assesment_mapping_parent_assesment_idToassesment.forEach((val) => {
                            single_blanko["nested_jawaban"][val.assesment_assesment_mapping_assesment_idToassesment.id] = {
                                id: val.assesment_assesment_mapping_assesment_idToassesment.id,
                                opsi: val.opsi,
                                jawaban: '',
                            }
                            blanko_nested[val.assesment_assesment_mapping_assesment_idToassesment.id] = val.assesment_assesment_mapping_assesment_idToassesment;
                        });
                    }
                    if (element.tipe_soal == "multi_opsi") {
                        single_blanko['jawaban'] = Array(element.assesment_opsi.length).fill('');
                    }
                    blanko_form[element.id] = single_blanko;
                });
                setCheckBoxJawaban(blanko_form);
                setMasterSoal(soal_response);
                setPercentage(0 / soal_response.length);
                setJumlahSoal(soal_response.length);
                setCurrentSoal(soal_response[0]);
                setStepSoal(0);
            }).catch((err) => {
                console.error(err)
                //   setOpen(true)
            });

        axios
            .get(`${configApi.BASE_URL}/profile-mahasiswa`, {
                headers: {
                    Authorization: "Bearer " + token
                }
            })
            .then((response) => {
                let data = response.data.data;
                setMahasiswa(data);
            }).catch((err) => {
                console.error(err)
                //   setOpen(true)
            });

        axios
            .get(`${configApi.BASE_URL}/studi_kasus/${studi_kasus}/header`, {
                headers: {
                    Authorization: "Bearer " + token
                }
            })
            .then((response) => {
                let data = response.data.data;
                setFirstLetterUppercase(capitalizeFLetter(data.nama));
            }).catch((err) => {
                console.error(err)
                //   setOpen(true)
            });
    }, [])

    useEffect(() => {
        // let temp_check_box = checkBoxJawaban;
        // if (currentSoal?.tipe_soal == "multi_opsi") {
        //     temp_check_box[stepSoal] = Array(currentSoal?.assesment_opsi?.length).fill(null);
        // }
        // setCheckBoxJawaban(temp_check_box);
    }, [stepSoal])



    function handleClickNavigate(path) {
        navigate(path);
    }

    function checkMultipleJawaban(toCompare, keyFind) {
        keyFind.forEach((v, i) => {
            if (toCompare.indexOf(v) > -1) {
                return true;
            }
        });
        return false;
    }

    function setOpsiRadio(id, opsi, index) {
        let nextCheckBox = checkBoxJawaban;
        nextCheckBox[id].jawaban = opsi;
        setCheckBoxJawaban.bind(this, nextCheckBox);
        setNestedSoalFlag(index);
    }

    return (
        <>
            <section className="absolute top-0 w-full h-screen bg-slate-100">
                <div className="py-6 bg-gray-900 text-center text-white">
                    Assesment {firstLetterUppercase}
                </div>
                <div className={`w-full flex flex-col sm:flex-row flex-grow overflow-hidden bg-blue-500`}>
                    <main role="main" className="w-full flex-grow p-4 overflow-auto bg-slate-100 text-gray-700">
                        <div className={`h-full w-full bg-white p-6 rounded text-lg max-h-[80vh] flex flex-col space-y-5 overflow-y-auto`}>
                            {stepSoal != masterSoal.length - 1 && (
                                <div className="w-full flex justify-end">
                                    <button className="w-[240px]justify-end text-center bg-blue-500 hover:bg-slate-200 text-white hover:text-gray-600 border-1 border-neutral-700 hover:border-neutral-500 font-bold py-2 px-4 rounded inline-flex items-center"
                                        type="button"
                                        onClick={() => {
                                            let newStep = stepSoal + 1
                                            if (newStep < masterSoal.length) {
                                                setStepSoal(newStep)
                                                setCurrentSoal(masterSoal[newStep])
                                                setJumlahJawaban(newStep + 1)
                                                setPercentage((((newStep + 1) / masterSoal.length) * 100).toFixed(2))
                                                if (newStep == masterSoal.length - 1) {
                                                    setTextNext("Selesai")
                                                }
                                            }
                                        }} >
                                        <span className="text-center w-full">Selanjutnya</span>
                                    </button>
                                </div>
                            )}
                            <div className="underline">{currentSoal?.pertanyaan}</div>

                            <div>{currentSoal?.deskripsi}</div>
                            {currentSoal?.assesment_opsi.map(function (key, index) {
                                if (currentSoal.tipe_soal == "multi_opsi") {
                                    return (<AssesmentCheckBox
                                        key={`${key.key_opsi}-${currentSoal.id}`}
                                        opsi={key.key_opsi}
                                        idx={index}
                                        label={key.label_opsi}
                                        refCheckBoxJawaban={checkBoxJawaban}
                                        refSetCheckBoxJawaban={setCheckBoxJawaban}
                                        idxSoal={currentSoal.id}
                                        soal={currentSoal}
                                    />);
                                } else {
                                    return (<div className={"flex items-center"} key={key.key_opsi}>
                                        <div className="flex flex-col">
                                            <div>
                                                <input checked={checkBoxJawaban[currentSoal.id].jawaban === key.key_opsi}
                                                    onChange={(e) => {
                                                        setOpsiRadio(currentSoal.id, key.key_opsi, index)
                                                    }}
                                                    id={"checked-checkbox-" + `${key.key_opsi}-${currentSoal.id}`}
                                                    name="opsi"
                                                    type="radio"
                                                    value={key.key_opsi}
                                                    className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600" />
                                                <label htmlFor={"checked-checkbox-" + `${key.key_opsi}-${currentSoal.id}`} className={"ml-2 text-md font-large text-gray-900 cursor-pointer"} onClick={() => {
                                                    setOpsiRadio(currentSoal.id, key.key_opsi, index)
                                                }}>{key.label_opsi}</label>
                                            </div>
                                        </div>
                                    </div>)
                                }
                            })}



                        </div>
                    </main>

                    <div className="sm:w-1/3 md:1/4 w-full flex-shrink flex-grow-0 p-4 bg-slate-100">
                        <div className="sticky p-4 w-full rounded bg-white h-[80vh] text-gray-700 flex flex-col justify-between">
                            <div className="grid grid-cols-4 gap-2">
                                {masterSoal?.map(function ({ id, pertanyaan, tipe_soal }, index) {
                                    let render_pertanyaan = pertanyaan;
                                    if (stepSoal === index) {
                                        render_pertanyaan = (<strong>{pertanyaan}</strong>);
                                    }
                                    let a_class = '';

                                    if (tipe_soal == "multi_opsi") {
                                        let keyFind = [];
                                        masterSoal[index].assesment_opsi.forEach((v, i) => {
                                            keyFind.push(v.key_opsi);
                                        })
                                        if (checkMultipleJawaban(checkBoxJawaban[id].jawaban, keyFind)) {
                                            a_class = ' bg-yellow-500 text-white';
                                        }

                                    } else if (checkBoxJawaban[id].jawaban) {
                                        a_class = ' bg-green-500 text-white';
                                    }

                                    if (index == stepSoal) {
                                        a_class = ' bg-purple-500 text-white';
                                    }


                                    return (
                                        <button className={"w-14 h-14 p-4 border-4" + a_class}
                                            onClick={() => {
                                                setCurrentSoal(masterSoal[index])
                                                setStepSoal(index)
                                                setCheckBoxJawaban(checkBoxJawaban)
                                                // if (index == masterSoal.length - 1) {
                                                //     setTextNext("Selesai")
                                                // } else {
                                                //     setTextNext("Selanjutnya")
                                                // }
                                            }} key={id}
                                        >
                                            {index + 1}
                                        </button>);
                                })}

                            </div>

                            <button className="text-center bg-red-500 hover:bg-slate-200 text-white hover:text-gray-600 border-1 border-neutral-700 hover:border-neutral-500 font-bold py-2 px-4 rounded inline-flex items-center"
                                type="button"
                                onClick={() => {
                                    handleOpen();
                                }} >
                                <span className="text-center w-full">Akhiri Ujian</span>
                            </button>

                        </div>
                    </div>
                </div>
                {/* <footer className="mt-auto bg-slate-100 h-auto max-w-6xl mx-auto flex justify-between text-white py-2">
                    {stepSoal != 0 && (
                        <button className={"pointer-cursor bg-blue-400 text-white border-1 border-neutral-700  font-bold py-2 px-4 rounded inline-flex items-center hover:bg-slate-200 hover:text-gray-600 hover:border-neutral-500"}
                            type="button"
                            // disabled={textNext == "Selesai"}
                            onClick={() => {

                                let newStep = stepSoal - 1
                                if (newStep >= 0) {
                                    setStepSoal(newStep)
                                    setCurrentSoal(masterSoal[newStep])


                                    setJumlahJawaban(newStep)
                                    setPercentage((((newStep) / masterSoal.length) * 100).toFixed(2))
                                }
                            }}
                        >
                            <span>Sebelumnya</span>
                        </button>
                    )}
                    {stepSoal == 0 && (
                        <div></div>
                    )}
                </footer> */}
            </section>
            {/* <section className="absolute top-0 left-0 w-full p-4" id="header">
                <button className="bg-white hover:bg-slate-400 text-gray-600 border-1 border-neutral-700 hover:border-neutral-500 font-bold py-2 px-4 rounded inline-flex items-center"
                    type="button"
                    onClick={() => handleClickNavigate('/studi-kasus/' + studi_kasus)}>
                    <span>Kembali</span>
                </button>
            </section> */}
            <section className="absolute top-0 right-0 p-4 text-white" id="header-mahasiswa">
                <div className="flex justify-end">
                    <div className="pr-5">
                        <p>{mahasiswa.nama}</p>
                        <p>{mahasiswa.nim}</p>
                    </div>
                    <img
                        alt='Avatar Mahasiswa'
                        src={mahasiswa?.imageUrl ?? 'https://ui-avatars.com/api/?background=random&name=Avatar'}
                        className="img-avatar-kasus rounded-full h-[45px]"
                    />
                </div>
            </section>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <Typography id="modal-modal-title" variant="h6" component="h2">
                        Terima kasih
                    </Typography>
                    <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                        Nilai anda : {nilai}
                    </Typography>
                </Box>
            </Modal>
        </>
    )
}